ul {
  flex-direction: column;
  gap: 30px;
  padding: 20px;
  list-style: none;
  display: flex;
}

ul li figcaption {
  padding: 20px;
  font-size: 20px;
  display: inline-block;
}

.list-item {
  width: 35%;
}

.player {
  margin: 0;
}

.btn {
  width: 300px;
  cursor: pointer;
  font-size: inherit;
  font-weight: light;
  letter-spacing: 1px;
  color: inherit;
  background: #f2f2f2;
  border: none;
  border-radius: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 20px 80px;
  transition: all .3s;
  display: inline-block;
  position: relative;
}

.btn:after {
  content: "";
  z-index: -1;
  background: #681abc;
  border-radius: 10px;
  transition: all .3s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 100%;
  transform: scale(.8);
}

.btn:hover, .btn:focus {
  color: #f8f8f8;
  background-color: rgba(0, 0, 0, 0);
}

.btn:hover:after, .btn:focus:after {
  right: 0;
  transform: scale(1);
}

